import React from "react";
import PropTypes from "prop-types";
import "./Movie.css"


function Movie(props) {
    return <div className="movie">
        <a href="">
            <img src={props.poster} alt={props.title} />
            <div calssName="movie_meta">
                <h3 className="movie_title">{props.title}</h3>
                <h5 className="movie_year">{props.year}</h5>
                <ul>
                    {props.genres.map((genre, index) => { return <li key={index} className="genres_genre">{genre}</li> })}
                </ul>
                <p className="movie_summary">{props.summary.slice(0,120)}...</p>
            </div>
        </a>

    </div>;
}

Movie.propTypes = {
    id: PropTypes.number.isRequired,
    year: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    summary: PropTypes.string.isRequired,
    poster: PropTypes.string.isRequired,
    genres: PropTypes.arrayOf(PropTypes.string).isRequired
}

export default Movie;

